// 4. PAGES [ 4.1. VENUE ] ############################################################################################

// A. GLOBAL STYLES +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

// A.1. COURT / VENUE PAGE --------------------------------------------------------------------------------------------

.tax-sp_venue {
  .o-article-header { margin: 0; padding: 0; }
  .o-block {
    &.event-list { margin-bottom: 50px;
      .row {
        > a { @include aspect-ratio(100%, 80%); margin: 0 auto $base-xl;
          #sp_openstreetmaps_container { @include position(absolute, 0); width: 100%!important; height: 100%!important; }
        }

        .a-match-day { width: 100%; font-size: 20px; padding: 15px 15px 5px; font-weight: 600; }

        .o-table-layout {
          &.event-list { width: 100%; padding: 15px 10px; 
            &:nth-of-type(even) { background: white!important; }
            .a-table-row { background: transparent;
              .a-table-cell { margin: 0; padding: 0; vertical-align: middle;
                &.a-match-up { width: 70%; font-weight: 300; }
                &.a-time,
                &.a-division { width: 15%; }
              }
            }
          }
        }
      }
    }
  }
}

// A.1. END -----------------------------------------------------------------------------------------------------------

// A.2. SINGLE TEAM PAGE ----------------------------------------------------------------------------------------------

.single-sp_team {
	.sp-section-content {
		&.sp-section-content-logo { width: 100%;
			.sp-team-logo { max-width: none; width: 100%; float: none; margin: 0;
				img { width: 100%; height: auto; max-width: 250px; margin: 50px auto; display: block; }
			}
		}
		&.sp-section-content-link {
			.sp-button { border: 1px solid $royal; padding: 8px 15px;
				&:hover { background: $blue-lt; }
			}
		}
		&.sp-section-content-details {
			.sp-team-details {
				.dt[home] { display: none; }
			}
		}
	}
}

// A.2. END -----------------------------------------------------------------------------------------------------------

// A.3. SINGLE EVENT PAGE ---------------------------------------------------------------------------------------------

.single-sp_event {
	.sp-section-content {
		&.sp-section-content-logos {
			.sp-template-event-logos {
				.sp-event-logos {
					.sp-team-logo {
						a {
							.sp-team-name {}
							img { max-width: 50px; height: auto; }
						}
					}
					.sp-team-result { font-size: 24px; }
					.sp-event-logos-time {
						&.sp-team-result { font-size: 32px; }
					}
				}
			}
		}
		&.sp-section-content-venue {
			table.sp-data-table {
				thead {
					tr {
						th {
							a { color: white; }
						}
					}
				}
				tbody {
					tr {
						td { padding: 0; }
					}
				}
			}
		}
		&.sp-section-content-details {
			table.sp-data-table { 
				thead {
					tr {
						th {
							&:nth-child(4) { display: none; }
						}
					}
				}
				tbody {
					tr {
						td {
							&:nth-child(4) { display: none; }
						}
					}
				}
			}
		}
	}
}

// A.3. END -----------------------------------------------------------------------------------------------------------

