// C. MIN-WIDTH: 768px ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

// B.1. MAX-WIDTH: 767px ----------------------------------

@media ($sm-dn) {

  .tax-sp_venue {
    .o-block {
      &.event-list {
        .row {
          .o-table-layout {
            &.event-list { width: 100%; padding: 10px 15px;
              .a-table-row {
                .a-table-cell { display: block; text-align: left;
                  &.a-match-up { width: 100%; }
                  &.a-time,
                  &.a-division { width: 50%; float: left; background: $grey-f9; padding: 3px 0; }
                }
              }
            }
          }
        }
      }
    }
  }

	.single-sp_event {
		.sp-section-content {
			&.sp-section-content-logos {
				.sp-template-event-logos {
					.sp-event-logos {
						.sp-team-logo {
							a { display: flex; flex-direction: column;
								.sp-team-name { order: 2; }
								img { max-width: 100px; margin: 0 auto; height: auto; order: 1; }
							}
						}
						.sp-team-result { font-size: 24px; }
						.sp-event-logos-time {
							&.sp-team-result { font-size: 32px; margin: 15px auto; display: block; }
						}
					}
				}
			}
		}
	}
	
}

// B.2. MIN-WIDTH: 768px ----------------------------------

@media ($sm-up) {

  .tax-sp_venue {
    .o-article-header { margin: 0; padding: 0; }
    .o-block {
      &.event-list {
        .row {
          > a { @include aspect-ratio(100%, 30%); margin: $base-xl auto ; }
        }
      }
    }
  }
}

// C. END +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

// D. MIN-WIDTH: 992px ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

@media ($md-up) {
	
	.single-sp_team {
		.sp-section-content {
			&.sp-section-content-details { width: 50%; display: inline-block; vertical-align: middle; }
			&.sp-section-content-logo { width: 48%; display: inline-block; vertical-align: middle;
				.sp-team-logo { max-width: none; width: 100%; float: none; margin: 0;
					img { width: 100%; height: auto; max-width: 250px; margin: 50px auto; display: block; }
				}
			}
		}
	}
	
}

// D. END +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

